@font-face {
  font-family: "Rounded_Elegance";
  src: local("Rounded_Elegance"),
    url("./assets/fonts/Rounded_Elegance.ttf") format("truetype");
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Rounded_Elegance";
  padding: 0;
  margin: 0;
}

html,
body {
  height: 100%;
  width: 100%;
  color: white;
  text-shadow: rgba(22, 71, 140, 0.2) 1px 0 4px;
  height: 100vh;
  overflow: hidden;
}

body {
  /* background: #3a7bd5;
  background: linear-gradient(to bottom, #00b8e0, #3a7bd5); */
  background: -webkit-linear-gradient(
    212deg,
    #fd8800,
    #fd008f,
    #9700fd,
    #003dfd,
    #05c7e6,
    #4bd58d
  );
  background: -o-linear-gradient(
    212deg,
    #fd8800,
    #fd008f,
    #9700fd,
    #003dfd,
    #05c7e6,
    #4bd58d
  );
  background: linear-gradient(
    238deg,
    #fd8800,
    #fd008f,
    #9700fd,
    #003dfd,
    #05c7e6,
    #4bd58d
  );
  background-size: 1200% 1200%;
  -webkit-animation: rainbow 30s ease infinite;
  animation: rainbow 30s ease infinite;
  background-attachment: fixed;
  text-align: center;
}

h1 {
  margin: auto;
  margin-top: 25px;
  margin-bottom: 30px;
  font-weight: 700;
  -webkit-transition: all 0.1s ease-out;
  -o-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

h2 {
  font-size: 1.5rem;
}

input[type="text"] {
  outline: none;
  padding: 0.5rem;
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: rgba(0, 0, 0, 0.4);
  font-weight: normal;
}
::-moz-placeholder {
  /* Firefox 19+ */
  color: rgba(0, 0, 0, 0.4);
  font-weight: normal;
}
:-ms-input-placeholder {
  /* IE 10+ */
  color: rgba(0, 0, 0, 0.4);
  font-weight: normal;
}
:-moz-placeholder {
  /* Firefox 18- */
  color: rgba(0, 0, 0, 0.4);
  font-weight: normal;
}

.container {
  padding: 1.5rem;
  overflow: hidden;
}

input.search {
  width: 100%;
}

.searchResults {
  height: 400px;
  padding: 1rem 0;
  overflow-y: scroll;
  overflow-x: scroll;
}

.searchResults-hidden {
  display: none;
}

.stationLink {
  background-color: rgba(255, 255, 255, 0.3);
  padding: 0.8rem;
  color: white;
  font-size: 1.25rem;
  text-align: left;
  text-shadow: none;
  cursor: pointer;
}

.stationLink .location {
  color: white;
  font-size: 0.9rem;
}

.stationLink:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.flash {
  position: absolute;
  top: 80px;
  width: 100%;
  text-align: center;
  opacity: 0;
  -webkit-transition: opacity 0.1s ease-in;
  -o-transition: opacity 0.1s ease-in;
  transition: opacity 0.1s ease-in;

  &.visible {
    opacity: 1;
    z-index: 999;
  }

  > span {
    background: rgba(78, 214, 245, 0.36);
    padding: 10px 20px;
    border-radius: 30px;
  }
}

/* ----- Animations ----- */

@-webkit-keyframes rainbow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes rainbow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}



