img.flowflow-logo {
  width: 50vw;
  max-width: 250px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

img.flowflow-logo-search-focused {
  width: 25vw;
  max-width: 125px;
  margin-left: auto;
  margin-right: auto;
  margin-top: -90px;
  display: block;
}

.slogan {
  margin-top: -20px;
  margin-bottom: 20px;
}

.slogan-hidden {
  display: none;
}

.fish1 {
  z-index: -10;
  top: 230px;
  height: 50px;
  width: 50px;
  left: -150px;
  position: relative;
  -webkit-animation-name: move;
          animation-name: move;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}

@-webkit-keyframes move {
  0% {
    -webkit-transform: translate(-150px, 0);
            transform: translate(-150px, 0);
  }

  40% {
    -webkit-transform: translate(500px, -50px);
            transform: translate(500px, -50px);
  }

  100% {
    -webkit-transform: translate(500px, -50px);
            transform: translate(500px, -50px);
  }
}

@keyframes move {
  0% {
    -webkit-transform: translate(-150px, 0);
            transform: translate(-150px, 0);
  }

  40% {
    -webkit-transform: translate(500px, -50px);
            transform: translate(500px, -50px);
  }

  100% {
    -webkit-transform: translate(500px, -50px);
            transform: translate(500px, -50px);
  }
}



